@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700&display=swap&subset=latin-ext);

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 22px;
  background: -webkit-gradient(linear, left top, left bottom, from(#e66465), to(#9198e5));
  background: linear-gradient(#e66465, #9198e5);
}

.App_wrapper__2wk79 {
  display: grid;
  min-height: 100vh;
  grid-template-rows: auto 1fr;
  max-width: 800px;
  margin: 0 auto;
}
.Input_wrapper__2xxDy {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Input_wrapper__2xxDy input {
  width: 70%;
  margin: 20px 0;
  min-width: 260px;
  max-width: 360px;
  flex-basis: auto;
  font-size: 0.9em;
  padding: 10px 5px;
  text-align: center;
  border-radius: 50px;
  outline: none;
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
